import axios from 'axios'

export const getAllSeasons = () => {
    return axios.get('https://rickandmorty-json-server.herokuapp.com/seasons')
}
export const getAllCaracters = (page) => {
    return axios.get(`https://rickandmortyapi.com/api/character/?page=${page}`)
}
export const getUsers = () => {
    return axios.get('https://rickandmorty-json-server.herokuapp.com/users')
}
export const postUsers = (user) => {
    return axios.post('https://rickandmorty-json-server.herokuapp.com/users', user)
}
